

body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #ffffff;
  color: #000000;
}

.app-container {
  width: 100%;
  height: 100%;
}

h1, h2, h3 {
  margin: 10px 0;
}

.input-group {
  margin: 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.game-id-input {
  padding: 8px;
  font-size: 16px;
  width: 200px;
  text-align: center;
}

.primary-button {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
background: linear-gradient(135deg, rgb(0, 122, 223), rgb(0, 236, 188));
  color: #fff;
  border: none;
  cursor: pointer;
}

.button-start {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
background: linear-gradient(135deg, rgb(0, 122, 223), rgb(0, 236, 188));
  color: #fff;
  border: none;
  cursor: pointer;
}

.primary-button:hover {
  opacity: 0.9;
}

.questions-list {
  width: 100%;
  max-width: 600px;
}

.question-item {
  margin-bottom: 20px;
}

.question-text {
  font-weight: bold;
  margin-bottom: 5px;
}

.options-list {
  list-style-type: none;
  padding-left: 20px;
}

.options-list li {
  margin: 3px 0;
}

.feud-list p {
  margin: 3px 0;
}

.slide-container {
  background-image: url('/public/trivia-app.PNG');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 40vh;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  margin: 0;
  box-sizing: border-box;
  max-width: 100%;
  box-sizing: border-box;
}

.welcome-image {
  width: 70vh;
  padding: 10px;
  color: #fff;
}

.breaktime-image {
  width: 70vh;
  padding: 10px;
  color: #fff;
}

.sec-container {
  background-image: url('/public/Slide3.PNG');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  padding: 10px;
  color: #fff;
}

.lastpage-container {
  background-image: url('/public/trivia-app.PNG');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.round1-container {
  background-image: url('/public/Slide4.PNG');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  padding: 10px;
  color: #fff;
}

.section-image{
  margin-bottom: auto;
  width: 15%;
  margin: 10px;
  padding: 0;
  line-height: 1;
}

.lastsec-image{
  width: 70vh;
  padding: 10px;
  color: #fff;
}

.sectionNum{
  font-size: 8rem;
  font-weight: bold;
  margin: 0;
  padding: 0;
  line-height: 1;
}

.catName{
  font-size: 1.8rem;
  font-weight: bold;
  margin: 5rem;
  padding: 0;
  line-height: 1;
}

.dataTitle{
  height: 10px;
  font-size: 1rem;
  font-weight: bold;
  color: #be7ebb;
  margin: 0;
  padding: 0;
  line-height: 1;
}

.slide-container h3, h1{
  margin-top: 0rem;
}

.question-text{
  font-size: 16px;
}

.input-answer{
  width: 80%;
  padding: 10px;
  border-radius: 10px;
}

.audio-question button {
  display: inline-block;
}

/*Rules Page */
/* styles.css */

.rules-container {
  width: 100%;
  padding: 0;
}

.rules-container ol {
  padding-left: 0;
  list-style-position: inside;
}

.rules-container li {
  font-size: 23px;
  margin-bottom: 13px;
  line-height: 1.5;
  word-wrap: break-word;
}

/* Big "Rules:" header */
.rules-title {
  color: #addbe4;
  font-weight: bold;
  font-size: 40px;
  text-align: center;
  margin-bottom: 30px;
}

/* Fixed Next button at bottom */
.fixed-next-button {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 20px;
  padding: 12px 30px;
  z-index: 100;
} 

/* Mobile-specific adjustments */
@media (max-width: 600px) {
  .rules-container li {
    font-size: 16px;
  }

  h1 {
    font-size: 22px !important;
    text-align: center;
  }

  .fixed-next-button {
    width: 90%;
    font-size: 18px;
  }
}
